import { Badge, Button, Jumbotron, Row, Table } from "react-bootstrap";

import React from "react";

enum GraphSize {
    Small,
    Medium,
    Large,
}

enum ClusterType {
    NonClustered,
    Clustered,
}

enum ReferenceType {
    URL,
    String,
}

export default class SampleData extends React.Component {
    private static createSizeBadge(size: GraphSize): JSX.Element {
        switch (size) {
            case GraphSize.Small:
                return <Badge variant="primary">Small Size</Badge>;
            case GraphSize.Medium:
                return <Badge variant="secondary">Medium Size</Badge>;
            case GraphSize.Large:
                return <Badge variant="warning">Large Size</Badge>;
        }
    }

    private static createClusterBadge(clusterType: ClusterType) {
        switch (clusterType) {
            case ClusterType.Clustered:
                return <Badge variant="primary">Clustered Graph</Badge>;
            case ClusterType.NonClustered:
                return <Badge variant="secondary">Non-Clustered Graph</Badge>;
        }
    }

    private createTableLine(
        name: string,
        label: (GraphSize | ClusterType)[],
        description: string,
        reference: { type: ReferenceType; value: string }
    ) {
        let renderBadges = label.map((value) => {
            if (value as GraphSize) {
                return SampleData.createSizeBadge(value as GraphSize);
            } else {
                return SampleData.createClusterBadge(value as ClusterType);
            }
        });

        let renderReference = null;
        if (reference.type === ReferenceType.URL) {
            renderReference = <a href={reference.value}>{reference.value}</a>;
        } else {
            renderReference = reference.value;
        }

        return (
            <tr>
                <th scope="row">{name}</th>
                <th>{renderBadges}</th>
                <td>{description}</td>
                <td>{renderReference}</td>
            </tr>
        );
    }

    render() {
        return (
            <Jumbotron
                className="p-4 p-md-5 text-white rounded"
                style={{ backgroundColor: "#93c2d2" }}
            >
                <h1 className="text-center mb-4">Sample Datasets</h1>
                <Table className="text-light">
                    <thead>
                    <tr>
                        <th scope="col">Name</th>
                        <th scope="col">Mark</th>
                        <th scope="col">Description</th>
                        <th scope="col">Reference</th>
                    </tr>
                    </thead>
                    <tbody>
                    {this.createTableLine(
                        "Les Miserables",
                        [GraphSize.Medium, ClusterType.NonClustered],
                        "the Les Miserables Character Relationship graph",
                        {
                            type: ReferenceType.URL,
                            value:
                                "https://www-cs-faculty.stanford.edu/~knuth/sgb.html"
                        }
                    )}
                    {this.createTableLine(
                        "EuroSiS web mapping study",
                        [GraphSize.Large, ClusterType.Clustered],
                        "Mapping interactions between Science in Society actors on the Web of 12 European countries",
                        {
                            type: ReferenceType.URL,
                            value:
                                "http://www.webatlas.fr/exhibition/eurosis/"
                        }
                    )}
                    {this.createTableLine(
                        "Internet",
                        [GraphSize.Large, ClusterType.Clustered],
                        "a symmetrized snapshot of the structure of the Internet at the level of autonomous systems.",
                        {
                            type: ReferenceType.URL,
                            value: "http://routeviews.org/"
                        }
                    )}
                    {this.createTableLine(
                        "Diseasome",
                        [GraphSize.Medium, ClusterType.Clustered],
                        "A network of disorders and disease genes linked by the known disorder–gene associa- tions, indicating the common genetic origin of many diseases.",
                        {
                            type: ReferenceType.String,
                            value:
                                "The human disease network\n" +
                                "Kwang-Il Goh, Michael E. Cusick, David Valle, Barton Childs, Marc Vidal, Albert-László Barabási\n" +
                                "Proceedings of the National Academy of Sciences May 2007, 104 (21) 8685-8690; DOI: 10.1073/pnas.0701361104"
                        }
                    )}
                    {this.createTableLine(
                        "C. Elegans neural network",
                        [GraphSize.Small, ClusterType.NonClustered],
                        "A directed weighted network representing the neural network of C. Elegans. Data compiled by D. Watts and S. Strogatz",
                        {
                            type: ReferenceType.String,
                            value:
                                "D. J. Watts and S. H. Strogatz, Nature 393, 440-442 (1998)."
                        }
                    )}
                    {this.createTableLine(
                        "CPAN authors",
                        [GraphSize.Small, ClusterType.NonClustered],
                        "CPAN Explorer is a visualization project aiming at analyzing the relationships between the developers and packages of the Perl language, known as the CPAN community.",
                        {
                            type: ReferenceType.URL,
                            value: "http://cpan-explorer.org/"
                        }
                    )}
                    {this.createTableLine(
                        "Coauthorships in network science",
                        [GraphSize.Small, ClusterType.NonClustered],
                        "coauthorship network of scientists working on network theory and experiment, as com- piled by M. Newman in May 2006.\n",
                        {
                            type: ReferenceType.String,
                            value:
                                "M.E.J.Newman,Phys.Rev.E74,036104(2006)."
                        }
                    )}
                    {this.createTableLine(
                        "Marvel Social Network",
                        [GraphSize.Large, ClusterType.Clustered],
                        "Networks of superheroes, constructed by Cesc Rossell ́o, Ricardo Alberich, and Joe Miro from the University of the Balearic Islands.",
                        {
                            type: ReferenceType.String,
                            value: ""
                        }
                    )}
                    {this.createTableLine(
                        "Power grid",
                        [GraphSize.Large, ClusterType.NonClustered],
                        "An undirected, unweighted network representing the topology of the Western States Power Grid of the United States.\n",
                        {
                            type: ReferenceType.String,
                            value:
                                "D.J.WattsandS.H.Strogatz,Nature393,440-442(1998)."
                        }
                    )}
                    </tbody>
                </Table>
                <div className="mb-1 d-flex justify-content-center align-items-center ml-3 mr-3">
                    <Row>
                        <Button
                            href="https://www.mediafire.com/file/wwd0bdp82kmbdgc/samples.zip/file"
                            variant="primary"
                            className="btn-lg d-flex justify-content-center align-items-center"
                        >
                            Download All Datasets
                        </Button>
                    </Row>
                </div>
            </Jumbotron>
        );
    }
}
